import axios from "axios";
import Config from "../config.js";

function resolveErrorResponse(error) {
	let msg = "";
	if (error.response && error.response.data && error.response.data.errors && error.response.data.errors.details) {
		for (let i = 0; i < error.response.data.errors.details.length; i++) {
			msg = msg + error.response.data.errors.details[0].message;
			if ((i + 1) < error.response.data.errors.details.length) {
				msg = msg + " - ";
			}
		}
	}
	return (msg === "" ? null : msg);
}

const SoamonitorService = (function () {
	let _service = null;
	
	function _getService() {
		if (!_service) {
			_service = this;
			return _service;
		}
		return _service;
	}
	
	function _getMonitorItems(criteria, env) {
		let url = "/soamonitor/monitor/items";
		let params = {"params": criteria};
		if (env.toLowerCase() !== Config.consoleEnvironment) {
			params.params.proxyRoute = url;
			url = "/soajs/proxy";
			params.headers = {
				"__env": env.toLowerCase()
			};
		}
		return axios.get(url, params)
			.then(res => {
				if (res && res.data && res.data) {
					return Promise.resolve(res.data.data);
				} else {
					return Promise.reject(new Error("Unable to proceed please try again later"));
				}
			})
			.catch((error) => {
				let msg = resolveErrorResponse(error);
				msg = msg || "Unable to proceed please try again later";
				return Promise.reject(new Error(msg));
			});
	}
	
	function _getMonitorAnalytics(type, criteria, env) {
		let url = "/soamonitor/monitor/analytics/" + type;
		let params = {"params": criteria};
		if (env.toLowerCase() !== Config.consoleEnvironment) {
			params.params.proxyRoute = url;
			url = "/soajs/proxy";
			params.headers = {
				"__env": env.toLowerCase()
			};
		}
		return axios.get(url, params)
			.then(res => {
				if (res && res.data && res.data) {
					return Promise.resolve(res.data.data);
				} else {
					return Promise.reject(new Error("Unable to proceed please try again later"));
				}
			})
			.catch((error) => {
				let msg = resolveErrorResponse(error);
				msg = msg || "Unable to proceed please try again later";
				return Promise.reject(new Error(msg));
			});
	}
	
	return {
		getService: _getService,
		getMonitorItems: _getMonitorItems,
		getMonitorAnalytics: _getMonitorAnalytics
	}
})();
export default SoamonitorService;