import axios from "axios";


function resolveErrorResponse(error) {
	let msg = "";
	if (error.response && error.response.data && error.response.data.errors && error.response.data.errors.details) {
		for (let i = 0; i < error.response.data.errors.details.length; i++) {
			msg = msg + error.response.data.errors.details[0].message;
			if ((i + 1) < error.response.data.errors.details.length) {
				msg = msg + " - ";
			}
		}
	}
	return (msg === "" ? null : msg);
}

